#header {
  height: 6.4rem;
  background-color: rgba(0, 0, 0, 0.3);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  border-bottom-color: darkcyan;
  border-bottom-style: solid;
  border-bottom-width: 0.1rem;
}

.title {
  font-size: 2.4rem;
  color: white;
  margin-left: 1.6rem;
}

.logo {
  margin-left: 3.2rem;
}
.logo img {
  height: 3.2rem;
  margin-left: 3.2rem;
  cursor: pointer;
}
.home-logo {
  position: absolute;
  height: 4rem;
  right: 3.2rem;
  -ms-flex-item-align: center;
      align-self: center;
  cursor: pointer;
}



